import { makeStyles } from '@material-ui/core';
import React from 'react';
import Helmet from 'react-helmet';
import { TwitterIcon, TwitterShareButton } from 'react-share';

import Layout from '../components/layout';
import PageNavigation from '../components/page-navigation';

const useStyles = makeStyles(theme => ({
  spaceAbove: {
    marginTop: theme.spacing(3)
  }
}));

const CorrectHard = () => {

  const classes = useStyles();

  return (
    <Layout>
      <section id="header">
        <div className="inner">
          <h1>正解！完全クリアです！</h1>
        </div>
      </section>
      <Helmet title="クリア | 氷蔵姉妹の挑戦" />
      <section id="one" className="main style1">
        <div className="grid-wrapper">
          <div className="col-12">
            <p>
              おめでとうございます！<br/>
              難易度ハード、クリアです！素晴らしいです！<br/>
              自分で作っておいてなんですが、ここまで遊んでいただいて、とても嬉しく思っております。
            </p>
            <p>
              遊んでいただきありがとうございました！<br />
              もしよろしければ、 #氷蔵姉妹の挑戦 のタグをつけて、感想を呟いて頂けると嬉しいです！
            </p>
            <div>
              <div>結果をシェアする</div>
              <TwitterShareButton
                url="https://contents.reizouko.online/riddle20200831/"
                title="謎解きを完全クリア (ハードモード) しました！"
                hashtags={["氷蔵姉妹の挑戦"]}
                className="custom-button"
              >
                <TwitterIcon size={48} round />
              </TwitterShareButton>
            </div>
          </div>
        </div>
        <div className={`grid-wrapper ${classes.spaceAbove}`}>
          <div className="col-12">
            <PageNavigation prev="/question/" />
          </div>
        </div>
      </section>
    </Layout>
  )
};

export default CorrectHard;